<template>
  <div
    class="chart-box"
    :style="{ height: height + 'px', width: width + 'px' }"
  ></div>
</template>
<script>
export default {
  props: {
    height: {
      type: String,
      default: ""
    },
    width: {
      type: String,
      default: ""
    },
    options: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  mounted() {
    this.draw();
  },
  methods: {
    draw() {
      let myChart = this.$echarts.init(this.$el);
      myChart.setOption(this.options);
    }
  }
};
</script>
<style lang="less" scoped>
.chart-box {
  min-height: 70px;
}
</style>
